<template>
  <div class="edit-question-container">
    <div class="edit_left_nav">
      <ul class="left_nav">
        <li class="edit">
          <a @click="navTo('questions')">
            <div :class="{
              'blue_icon': activeTab == 'questions',
              'wihte_icon': activeTab != 'questions'
            }"><i></i></div>题型
          </a>
        </li>
        <li class="facade">
          <a @click="navTo('appearance')">
            <div :class="{
              'blue_icon': activeTab == 'appearance',
              'wihte_icon': activeTab != 'appearance'
            }"><i></i></div>外观
          </a>
        </li>
        <li class="set_show">
          <a @click="navTo('logic')">
            <div :class="{
              'blue_icon': activeTab == 'logic',
              'wihte_icon': activeTab != 'logic'
            }"><i></i></div>逻辑
          </a>
        </li>
      </ul>
    </div>
    <router-view :key="$route.fullPath"></router-view>
  </div>
</template>

<script>
  import api from '~/api/api';
  import {mapState} from 'vuex';
  import store from '~/store/index';
  import * as types from '~/store/mutation-type';
  import {getStore} from '~/tools/utils';
  
  export default {
    computed: {
      ...mapState({
          step: state => state.step
      })
    },
    data() {
      return {
        name: getStore('name'),
        activeTab: 'questions' //appearance, logic
      }
    },
    mounted() {
      this.activeTab = this.$route.fullPath.split('/')[4];
    },
    methods: {
      navTo(path) {
        this.activeTab = path;
        this.$router.push({ path: `/edit/${this.$route.params.sid}/question/${path}` });
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .edit-question-container {
    min-height: 100%;
    width: 100%;
    background #EEEFF1
    overflow: hidden;
    .edit_left_nav{
        width: 71px;
        height: 100%;
        overflow: auto;
        background-color: #ffffff;
        position: fixed;
        top: 66px;
        left: 0;
        z-index: 299;
        text-align: center;
        border-right:1px solid #d8d8d8;
        .left_nav {
          .edit {
            .wihte_icon i{
              background-position: -360px -96px;
            }
            .blue_icon i{
              background-position: -384px -96px;
            }
          }
          .facade {
            .wihte_icon i{
              background-position: -360px -120px;
            }
            .blue_icon i{
              background-position: -384px -120px;
            }
          }
          .set_show {
            .wihte_icon i{
              background-position: -408px -72px;
            }
            .blue_icon i{
              background-position: -432px -72px;
            }
          }
          li {
            .facade{
              position: relative;
            }
            &.edit {
              a {
                div {
                  margin:24px 0 0 18px;
                }
              }
            }
            i {
              cursor: pointer;
              width: 24px;
              height: 24px;
              background: url(../../images/icons.png) no-repeat;
              background-size: 520px auto;
              vertical-align: middle;
              position: absolute;
              top: 6px;
              left: 6px;
            }
            a {
              font-size: 12px;
              color: #484848;
              div {
                width: 36px;
                height: 36px;
                border-radius: 4px;
                position: relative;
                margin:19px 0 0 18px;
              }
            }
          }
        }
    }
    .wihte_icon{
      background-color: #fff;
    }
    .blue_icon{
      background-color: #2672ff;
    }
  }
</style>



